#playerStats {
  width: 15rem;
  height: 4rem;
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  top: 3rem;
  left: 3rem;
}

h1 {
  margin: 0;
  font-size: 72px;
}

h2 {
  margin: 0;
  font-size: 30px;
}

#playerStats__lines {
  display: flex;
  align-items: center;
}

#playerStats__lines__thick {
  display: block;
  background-color: white;
  height: 0.2rem;
  width: 8.75rem;
}

#playerStats__lines__thin {
  display: block;
  background-color: lightgray;
  height: 0.1rem;
  width: 6.2rem;
}

@media (max-width: 1100px) {
	#playerStats {
		left: 55pt;
		transform: translateX(-50%);
		width: 8rem;
		height: 2.5rem;
		top: 2rem;
	}

	h1 {
		font-size: 29px;
	}

	h2,
	h3,
	p {
		font-size: 21px;
	}
}

@media (max-width: 1024px) {
	.p-container p{
		font-size: 100%;
	}
	.p-container{
		background-color: rgb(17, 0, 34);
		opacity: 0.8;
	}
}
